import { storageService } from './storage.service'
import { utilService } from './util.service'
import { httpService } from './http.service'

export const botService = {
    save,
    query,
    getById,
    remove,
}

const STORAGE_KEY = 'bot'
const BOT_BASE_ENDPOINT = 'bot'


async function query() {
    let botsFromDB = await httpService.get(BOT_BASE_ENDPOINT)
    return botsFromDB
}

async function getById(botId) {
    return await httpService.get(`${BOT_BASE_ENDPOINT}/${botId}`)
}

async function remove(botId) {
    await storageService.remove(STORAGE_KEY, botId)
}

async function save(bot) {
    if (bot._id) {
        const savedBot = await httpService.put(BOT_BASE_ENDPOINT, bot)
        return savedBot
    } else {
        try {
            const savedBot = await httpService.post(BOT_BASE_ENDPOINT, bot)
            return savedBot
        } catch (err) {
            console.log(err)
        }
    }
}