import { botService } from "../../services/bot.service"

export function getActionAddBot(bot) {
    return { type: 'UPDATE_BOT', bot }
}
export function getActionUpdateBot(bot) {
    return { type: 'UPDATE_BOT', bot }
}
export function getActionSetBot(board) {
    return { type: 'SET_BOT', board }
}

export function loadBot(botId) {
    return async (dispatch) => {
        try {
            const botFromDb = await botService.getById(botId)
            dispatch(getActionSetBot(botFromDb))
        } catch (err) {
            console.log('Cannot load boards', err)
        }
    }
}

export function loadBots() {
    return (dispatch) => {
        botService.query()
            .then(bots => {
                dispatch({ type: 'SET_BOTS', bots })
            })
            .catch(err => {
                console.log('Cannot load boats', err)
            })
    }
}

export function removeBot(botId) {
    return async dispatch => {
        try {
            await botService.remove(botId)
            dispatch({ type: 'REMOVE_BOT', botId })
        } catch (err) {
            console.error('Error:', err)
        }
    }
}

export function addBot(bot) {
    return (dispatch) => {
        botService.save(bot)
            .then(savedBot => {
                console.log('Added bot', savedBot);
                dispatch(getActionAddBot(savedBot))
            })
            .catch(err => {
            })
    }
}

export function updateBot(bot) {
    return (dispatch) => {
        botService.save(bot)
            .then(savedBot => {
                console.log('Updated bot:', savedBot);
                dispatch(getActionUpdateBot(savedBot))
            })
            .catch(err => {
                console.log('Cannot save bot', err)
            })
    }
}