import { useEffect } from "react"
import { useSearchParams } from "react-router-dom"

export const Params = () => {

  const [queryParameters] = useSearchParams()
  const [searchParams] = useSearchParams()

  let myActions = [{
    actions: {
      type: queryParameters.get("type"),
      text: queryParameters.get("text"),
    }
  }]

  useEffect(() => {
    console.log(JSON.stringify({ myActions }))
  }, [queryParameters, searchParams])

  return (
    <div>
      {JSON.stringify(myActions)}
      <br /><br />
      <p>text: <strong>{queryParameters.get("text")} </strong></p>
      <p>Type: <strong>{queryParameters.get("type")} </strong></p>
    </div>
  )
}