
const initialState = {
    bot: null,
    bots: [],
    filterBy: {},
    lastRemovedBot: null

}

export function botReducer(state = initialState, action) {
    var newState = state
    var bots

    switch (action.type) {
        case 'SET_BOT':
            bots = state.bots.map(bot => {
                return (bot._id === action.bot._id) ? action.bot : bot
            })
            newState = { ...state, bot: { ...action.bot }, bots }
            break
        case 'SET_BOTS':
            newState = { ...state, bots: action.bots }
            break
        case 'ADD_BOT':
            newState = { ...state, bots: [...state.bots, action.bot] }
            break
        case 'UPDATE_BOT':
            bots = state.bots.map(bot => (bot._id === action.bot._id) ? action.bot : bot)
            newState = { ...state, bots }
            break
        case 'REMOVE_BOT':
            const lastRemovedBot = state.bots.find(bot => bot._id === action.botId)
            bots = state.bots.filter(bot => bot._id !== action.botId)
            newState = { ...state, bots, lastRemovedBot }
            break
        case 'SET_FILTERBY':
            return { ...state, filterBy: action.filterBy }
        default:
    }

    // For debug:
    window.botState = newState
    // console.log('Prev State:', state)
    // console.log('Action:', action)
    // console.log('New State:', newState)
    return newState


}