import React, { useState } from 'react'
import Hebcal from 'hebcal'
import { GetUrlParams } from '../get-url-params'
import { useDispatch } from 'react-redux'
import { updateBot } from '../../store/actions/bot.action'


export const JewishConvert = () => {
  const [gregorianDate, setGregorianDate] = useState('')
  const [jewishDate, setJewishDate] = useState('')
  const dispatch = useDispatch()

  // Define an object to hold the input values
  const [inputValues, setInputValues] = useState({
    gregorianDate: '',
    jewishDate: ''
  })

  const convertToJewishDate = (ev) => {
    ev.preventDefault()

    const regex = /^(\d{1,2})[-.,/]?(\d{1,2})[-.,/]?(\d{4})$/
    const match = gregorianDate.match(regex)
    if (match) {
      const [, month, day, year] = match
      const hebrewDate = new Hebcal.HDate(new Date(year, month - 1, day || 1))
      setJewishDate(hebrewDate.toString('h'))

      setInputValues({ ...inputValues, gregorianDate, jewishDate: hebrewDate.toString('h') })
      dispatch(updateBot(inputValues))
    } else {
      alert('Invalid date format. Please enter a date in the format MM/DD/YYYY, MM-DD-YYYY, or MM.DD.YYYY.')
    }
  }

  return (
    <div className="jewish-convert-container">
      <form className='jewish-convert-form'>
        <label className="jewish-convert-label" htmlFor="gregorianDate">Enter a Gregorian date:</label>
        <p className="jewish-convert-note">Please enter a date in the format MM/DD/YYYY</p>
        <input className="jewish-convert-input" type="text" id="gregorianDate" value={gregorianDate} onChange={(e) => setGregorianDate(e.target.value)} placeholder="MM/DD/YYYY" />
        <button className="jewish-convert-button" onClick={convertToJewishDate}>Convert to Jewish date</button>
      </form>
      {jewishDate && <p className="jewish-convert-result">The Jewish date is {jewishDate}</p>}<br /><br />
      {jewishDate && <GetUrlParams inputDate={jewishDate} />}
    </div>
  )
}
